.sidebar {
  cursor: pointer;
  height: 32px;
  display: flex;
  align-items: center;
  width: 20px;
  background: #20b757;
  justify-content: center;
  border: none;
  color: #fff;
}

.collapsed {
  border-radius: 14px 0 0 14px;
}
.open {
  border-radius: 0 14px 14px 0;
}

.currency-select {
  width: 200px;
}

@media only screen and (max-width: 500px) {
  .currency-select {
    width: 160px !important;
  }
}
