.net-worth-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .graph-view {
    margin-top: 40px;
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    width: 100%;
    .filters {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px dashed #20b757;
      padding-bottom: 14px;
      .title {
        font-size: 18px;
        font-weight: 600;
      }
      .buttons {
        display: flex;
        align-items: center;
        gap: 10px;
        .ant-select {
          height: 30px;
          .ant-select-selector {
            background: #4372e90a;
          }
          .ant-select-selection-item {
            font-size: 12px;
          }
        }
        .ant-segmented-item-label {
          font-size: 12px;
        }
        .ant-segmented-item-selected {
          background: #20b757;
          color: #fff;
        }
        .ant-picker-outlined {
          border-radius: 32px;
          background: #20b75715;
        }
        .ant-picker .ant-picker-input > input {
          font-size: 14px;
          line-height: 22px;
        }
      }
    }
  }
  .summary-table {
    margin-top: 30px;
    .percent-difference {
      font-size: 12px;
      font-weight: 500;
      border: 1px solid;
      line-height: 23px;
      padding: 0 11px;
      border-radius: 10px;
      display: flex;
      gap: 5px;
      font-weight: 600;

      &.positive {
        color: #20b757;
        border-color: #20b757;
        background-color: #20b75715;
      }
      &.negative {
        color: #ff6161;
        border-color: #ff6161;
        background-color: #ff616115;
      }
    }

    .debt-cell {
      color: #ff6161;
    }
  }
}

@media only screen and (max-width: 500px) {
  .net-worth-container {
    .header {
      font-size: 18px;
    }
    .graph-view {
      margin-top: 10px;
      padding: 10px;
      .graph {
        overflow: auto;
      }
      .filters {
        flex-wrap: wrap;
        gap: 10px;
        .buttons {
          flex-wrap: wrap;
          gap: 10px;
          .ant-picker .ant-picker-input > input {
            font-size: 12px;
            line-height: 16px;
          }
        }
      }
    }
  }
}
