.budgeting-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .metrics {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .forecast {
    background: #fff;
    padding: 14px;
    border-radius: 12px;
    margin-top: 40px;

    .actions {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 30px;

      .title {
        font-size: 18px;
        font-weight: 600;
      }
      .buttons {
        display: flex;
        align-items: center;
        gap: 14px;
        .ant-btn {
          font-size: 12px;
          font-weight: 500;
        }
      }
    }
  }
  .account-cell {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-top: 4px;
    .icon {
      height: 30px;
      width: 30px;
      background: #205cb7;
      text-transform: capitalize;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      line-height: 20px;
      color: #fff;
    }
    .content {
      line-height: 20px;

      .main {
      }
      .meta {
        font-size: 12px;
        opacity: 0.5;
        font-weight: 400;
      }
    }
  }
  .cell-forecast {
    background: #20b7573b;
    color: #00000096;
  }
  .cell-overdue {
    background: #ff4d4f1a;
    color: #ff6161;
  }
  .cell-value {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.cell-value-popconfirm {
}

.cell-value-popconfirm .ant-popconfirm-title {
  font-weight: bold;
  color: #fa541c;
}

.cell-value-popconfirm .ant-popconfirm-description {
  font-size: 14px;
  color: #595959;
}

.cell-value-popconfirm .ant-btn-primary {
  padding: 10px;
  font-size: 12px;
}

.cell-value-popconfirm .ant-btn-default {
  padding: 10px;
  font-size: 12px;
}
