.payment-schedule-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .filters {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px dashed #20b757;
    .title {
      font-size: 18px;
      font-weight: 500;
    }
    .buttons {
      display: flex;
      gap: 20px;
      align-items: center;
      .ant-picker .ant-picker-input > input {
        font-size: 14px;
        line-height: 22px;
      }
      .ant-picker {
        border-radius: 32px;
        padding: 4px 16px;
      }
      .navigation {
        display: flex;
        gap: 6px;
        button {
          font-size: 12px;
          padding: 15px;
          border-radius: 50%;
        }
      }
      .payment-category {
        font-size: 14px;
      }
    }
  }
  .calendar-container {
    background: #fff;
    padding: 14px;
    border-radius: 12px;
    margin-top: 20px;
    .payments {
      display: flex;
      flex-direction: column;
      gap: 1px;
      overflow-y: auto;
      .payment-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 3px;
        border-radius: 5px;
        font-size: 11px;
        font-weight: 500;
        gap: 4px;
        .name {
          font-weight: 400;
          display: flex;
          align-items: center;
          .status {
            font-size: 12px;
          }
          .icon {
            height: 16px;
            margin-left: 8px;
            margin-right: 4px;
            background: #fff;
            border-radius: 50%;
            img {
              height: 100%;
            }
          }
        }

        &.success {
          border: 1px solid #20b757;
          background: #20b75715;
          .name {
            .status {
              color: #20b757;
            }
          }
        }
        &.failed {
          border: 1px solid #ff6161;
          background: #ff616115;
          .name {
            .status {
              color: #ff6161;
            }
          }
        }
        &.pending {
          border: 1px solid #bd7b00;
          background: #bd7b0015;
          .name {
            .status {
              color: #bd7b00;
            }
          }
        }
      }
    }

    .ant-picker-calendar.ant-picker-calendar-full .ant-picker-calendar-date {
      border: 1px solid #fff;
      margin: 0;
      overflow: auto;
      padding: 2px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-calendar-date-content {
      height: 75px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-calendar-date-today {
      border-color: #20b757;
    }
    .ant-picker-calendar.ant-picker-calendar-full .ant-picker-panel {
      text-align: start;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      td {
      border: 2px solid #ebecef;
    }
    .ant-picker-cell-selected {
      .ant-picker-cell-inner {
        background: #fff !important;
      }
    }

    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th {
      border: none;
      font-weight: 600;
      font-size: 0;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(1)::before {
      content: "Sunday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(2)::before {
      content: "Monday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(3)::before {
      content: "Tuesday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(4)::before {
      content: "Wednesday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(5)::before {
      content: "Thursday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(6)::before {
      content: "Friday";
      font-size: 16px;
    }
    .ant-picker-calendar.ant-picker-calendar-full
      .ant-picker-panel
      .ant-picker-body
      th:nth-child(7)::before {
      content: "Saturday";
      font-size: 16px;
    }
  }
}
