.table-container {
  background: #fff;
  padding: 20px 14px;
  border-radius: 12px;
  .breadcrumb {
    padding-bottom: 20px;
  }

  .ag-row.ag-full-width-row.ag-row-group > * {
    font-weight: 600;
  }
  .ag-header-cell-label {
    font-weight: 600;
  }
  .ag-root-wrapper {
    border: none;
  }
  .ag-row {
    border-bottom: 1px solid #ebecef;
  }
  .ag-row-hover {
    background: #20b75715 !important;
  }

  .ag-cell.ag-cell-not-inline-editing.ag-cell-normal-height.ag-cell-value {
    align-items: center;
    display: flex;
    justify-content: flex-start;
  }
  .account-cell {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-top: 4px;
    .icon {
      height: 30px;
      width: 30px;
      background: #205cb7;
      text-transform: capitalize;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      line-height: 20px;
      color: #fff;
    }
    .content {
      line-height: 20px;

      .main {
      }
      .meta {
        font-size: 12px;
        opacity: 0.5;
        font-weight: 400;
      }
    }
  }
  .category-cell {
    width: 100%;
    .ant-select {
      width: 100%;
    }
    .ant-select-selector {
      font-size: 14px;
      border-radius: 32px;
    }
  }
}

::ng-deep {
  .buttons {
    .ant-btn {
      font-size: 14px;
    }
  }
  .ant-radio-button-wrapper:last-child {
    border-start-end-radius: 30px;
    border-end-end-radius: 30px;
  }
}

.account-form-modal {
  .modal-footer {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    button {
      width: 100%;
    }
  }
}
